.project-body {
  width: 100%;
  background-color: #f0b30a;

  img {
    width: 100%;
  }

  article {
    margin: 0 (2 * $red_space);
    @media screen and (max-width: map.get($screens, "medium")) {
      margin: 0;
    }
  }

  p {
    font-size: 1.3rem;
    font-weight: 400;
    margin: 1.5rem (2 * $red_space);
    @media screen and (max-width: map.get($screens, "medium")) {
      margin: 1rem 1rem;
      font-size: 1rem;
    }
  }

  p:has(img) {
    margin: 0 0 0 0;
  }

  .project-header {
    margin: $red_space (2 * $red_space);

    @media screen and (max-width: map.get($screens, "medium")) {
      margin: 1rem 1rem;
    }
  }

  .card-e {
    margin-left: $red_space;
  }
}

#projects-section,
.project-body {
  .card-e {
    margin-top: $red_space;
    width: 50%;
    @media screen and (max-width: map.get($screens, "medium")) {
      width: 100%;
      margin-top: 0.75 * $red_space;
      margin-left: 0;
    }
  }
}
